import { useState, useCallback } from "react";
import "./App.css";
import CodeMirror from "@uiw/react-codemirror";
import { html } from "@codemirror/lang-html";
import { css } from "@codemirror/lang-css";
import { javascript } from "@codemirror/lang-javascript";

function App() {
  const [html_val, setHtml_val] = useState("");
  const [css_val, setCss_val] = useState("");
  const [js_val, setJs_val] = useState("");

  const onHtmlChange = useCallback((value) => {
    setHtml_val(value);
  }, []);

  const onCssChange = useCallback((value) => {
    setCss_val(value);
  }, []);

  const onJsChange = useCallback((value) => {
    setJs_val(value);
  }, []);

  let src = `<html>
  <body>${html_val}</body>
  <style>${css_val}</style>
  <script>${js_val}</script>
  </html>`;
  return (
    <>
      <div className="head">Rajat's Editor</div>
      <div className="input section">
        <div className="container">
          <div className="container-title">HTML</div>
          <CodeMirror
            className="code"
            value={html_val}
            theme="dark"
            extensions={[html(true)]}
            onChange={onHtmlChange}
          />
        </div>

        <div className="container">
          <div className="container-title">CSS</div>
          <CodeMirror
            className="code"
            value={css_val}
            theme="dark"
            extensions={[css(true)]}
            onChange={onCssChange}
          />
        </div>

        <div className="container">
          <div className="container-title">JAVASCRIPT</div>
          <CodeMirror
            className="code"
            value={js_val}
            theme="dark"
            extensions={[javascript(true)]}
            onChange={onJsChange}
          />
        </div>
      </div>
      <div className="output section">
        <iframe
          srcDoc={src}
          title="output"
          sandbox="allow-scripts"
          frameBorder={0}
          width="100%"
          height="100%"
        />
      </div>
    </>
  );
}

export default App;
